<script>
import HeaderTitle from "@/components/HeaderTitle";
import Service from "../utils/general";
import PDFService from "../utils/pdf";
import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
pdfMake.vfs = pdfFonts.pdfMake.vfs;
import { mapActions } from "vuex";
import axios from "axios";

export default {
  name: "admision-registro-a",
  components: {
    HeaderTitle,
  },
  data: () => ({
    search_loading: false,
    menu_date: false,
    check_deposit: false,
    datosBuscados:false,
    type_state: 1,
    modalidad: "",
    generos: [],
    lenguas: [],
    nacion_pueblos: [],
    departamentos: [],
    provincias: [],
    municipios: [],
    cantones: [],
    localidades: [],
    instituciones: [],
    especialidades: [],
    postulante: {
      id: "",
      cedula_dni: "",
      complemento: "",
      expedido_id: "",
      paterno: "",
      materno: "",
      nombres: "",
      fecha_nacimiento: "",
      genero_id: "",
      pais_id: "",
      departamento_id: "",
      direccion_departamento_id: "",
      provincia_id: "",
      municipio_id: "",
      localidad_id: "",
      parentesco: false,
      grupo_sanguineo_id: "",
      tiene_discapacidad: "NO",
      correo_electronico: "",
      celular: "",
      direccion: "",
      codigo_seguro: "",
      estado: 1,
      //Lengua Originaria de egreso
      lengua_id: "",
      nacion_pueblo_id: "",
      pueblo_nacion: "",
      //Unidad Educativa de egreso
      unidad_educativa_id: "",
      unidad_educativa: "",
      gestion: "",
      
      informacion_academica:"",

      convocatoria_id: "",
      modalidad_id: "",
      deposito: "",
      deposito_id: "",
      institucion_id: "",
      especialidad_id: "",

      parentesco_nombre: "",
      parentesco_cargo: "",

      direccion_zona: "",
      direccion_calle: "",
      direccion_nro: "",

      convocatoria: "",
      ruta_imagen: "",
      descripcion: "",
      tipo: "",
      foto: null,
    },
    
    cedula: "",
    fecha_nacimiento: "",
    complemento: "",
    checkDialog: false,
    send_loading: false,
    response_status: false,
    estado: "",
    message: "",
    fFotografia: null, // <- initialize the v-model prop
    imageData: null,
    reglasImage: [
      v => !!v || 'El archivo es Requerido',
      v => (v && v.size > 0) || 'El archivo es Requerido',
    ],
    return: {
      file: ''
    },
    direccion_requerido: true
  }

  ),
  mounted() {
    let params;
    if (Service.getModality()) {
      params = Service.getModality().split("|");
    } else {
      this.$router.replace({ name: "admision-inscripcion" });
    }
    this.postulante.convocatoria_id = params[0];
    this.postulante.modalidad_id = params[1];
    this.modalidad = params[2];
    this.getParameters();
    this.cargaConvocatoria();
    // let user = Service.getUser();
    /* if (user) {
        this.postulante.id = user.uid;
      } else {
        if (this.$router.currentRoute.name != "root") {
          this.$router.push({ name: "root" });
        }
      } */
  },
  computed: {
    birthDateFormatted() {
      if (!this.postulante.fecha_nacimiento) return null;

      const [year, month, day] = this.postulante.fecha_nacimiento.split("-");
      return `${day}/${month}/${year}`;
    },
  },
  methods: {
    ...mapActions(["toast"]),
    checkDeposit() {
      if (this.$refs.formnd.validate()) {
        this.check_deposit = true;
        axios
          .post(
            Service.getUrlAPI() + "verificar/deposito",
            {
              convocatoria_id: this.postulante.convocatoria_id,
              numero: this.postulante.deposito,
            },
            Service.getHeader()
          )
          .then((response) => {
            this.check_deposit = false;
            this.postulante.deposito_id = response.data.id;
            if (response.data.estado) {
              this.type_state = 3;
            } else {
              this.type_state = 2;
            }
          })
          .catch(() => {
            this.check_deposit = false;
          });
      } else {
        this.toast({ color: "info", text: "Ingrese número de depósito" });
      }
    },
    getParameters() {
      axios
        .post(
          Service.getUrlAPI() + "admision/parametro/registro",
          {
            convocatoria_id: this.postulante.convocatoria_id,
            modalidad_id: this.postulante.modalidad_id
          },
          Service.getHeader()
        )
        .then((response) => {
          this.departamentos = response.data.departamentos;
          this.generos = response.data.generos;
          this.instituciones = response.data.instituciones;
          this.lenguas = response.data.lenguas;
          this.nacion_pueblos = response.data.nacion_pueblos;
        })
        .catch(() => {
          console.error("Parametros no encontrado");
        });
    },
    getSpecialties() {
      this.especialidades = [];
      this.postulante.especialidad_id = "";
      axios
        .post(
          Service.getUrlAPI() + "institucion/especialidades",
          { institucion_id: this.postulante.institucion_id },
          Service.getHeader()
        )
        .then((response) => {
          this.especialidades = response.data;
        })
        .catch(() => {
          console.error("Especialidades no encontrado");
        });
    },
