          color: "info",
          text: "Ingrese Cédula de identidad y Fecha de nacimiento",
        });
      } else {
        this.datosBuscados = true;
        //en primera instancia se realiza la busqueda de los datos del postulante en una gestion anterior
        //si no se encuentra los datos se realiza la busqueda en el SIE
        this.search_loading = true;


        this.postulante.paterno = "";
        this.postulante.materno = "";
        this.postulante.nombres = "";

        this.postulante.genero_id = "";
        this.postulante.departamento_id = "";
        this.postulante.celular = "";
        this.postulante.correo_electronico = "";
