              // this.postulante.grupo_sanguineo_id = response.data.grupo_sanguineo_id;
              // this.postulante.tiene_discapacidad = response.data.tiene_discapacidad;
              this.postulante.unidad_educativa = student.institucioneducativa || "";
              this.postulante.gestion = student.gestion_tipo_id || "";
              // this.postulante.direccion = response.data.direccion;
              // this.postulante.correo_electronico = response.data.correo_electronico;
              this.postulante.celular = student.celular || "";
              //this.postulante.codigo_seguro = response.data.codigo_seguro;
              // this.postulante.lengua_id = response.data.lengua_id;
              // this.postulante.pueblo_nacion = response.data.pueblo_nacion;
            } else {
              axios
                .post(
                  Service.getUrlAPI() + "admision/validar/persona/egi",
                  {
                    cedula_dni: this.postulante.cedula_dni,
                    fecha_nacimiento: this.postulante.fecha_nacimiento,
