                    const student = response.data;
                    if (student != "") {
                      //this.postulante.id = response.data.id;
                      this.postulante.nombres = student.nombres;
                      this.postulante.paterno = student.paterno || "";
                      this.postulante.materno = student.materno || "";
                      this.postulante.celular = student.celular || "";
                      this.postulante.correo_electronico = student.correo_electronico || "";
                      this.postulante.direccion = student.direccion || "";
                      this.postulante.unidad_educativa = student.recorrido_academicos[0].unidad_educativa || "";
                      this.postulante.gestion = student.recorrido_academicos[0].gestion || "";
                      this.postulante.genero_id = student.genero_id || "";
                      this.postulante.departamento_id = student.departamento_id || "";

                      // this.postulante.genero_id = response.data.genero_id;
                      // this.postulante.pais_id = response.data.pais_id;
                      // this.postulante.departamento_id = response.data.departamento_id;
                      // this.postulante.provincia_id = response.data.provincia_id;
                      // this.postulante.localidad_id = response.data.localidad_id;

                      // this.postulante.grupo_sanguineo_id = response.data.grupo_sanguineo_id;
                      // this.postulante.tiene_discapacidad = response.data.tiene_discapacidad;
                      /* this.postulante.gestion = response.data.datUnidadEducaitva
                        ? response.data.datUnidadEducaitva.gestion
                        : ""; */
                      // this.postulante.direccion = response.data.direccion;
                      // this.postulante.correo_electronico = response.data.correo_electronico;
                      // this.postulante.celular = response.data.celular;
                      //this.postulante.codigo_seguro = response.data.codigo_seguro;
                      // this.postulante.lengua_id = response.data.lengua_id;
                      // this.postulante.pueblo_nacion = response.data.pueblo_nacion;
                    } else {
                      this.toast({ color: "info", text: "Registro no encontrado" });

                    }

                  } else {
                    this.toast({ color: "info", text: "Registro no encontrado" });
                  }
                }

                )
                .catch(() => {
                  this.search_loading = false;
                  console.error("Postulante no encontrado");
                });

            }
          })
          .catch(() => {
            this.search_loading = false;
            console.error("Postulante no encontrado");
          });
      }
    },

