    sendResult() {
      this.send_loading = true;
      this.cedula = this.postulante.cedula_dni;
      this.fecha_nacimiento = this.postulante.fecha_nacimiento;
      this.complemento = this.postulante.complemento;
      this.postulante.informacion_academica =  JSON.stringify("");
      this.cargarDatosFotografia();

      let fd = new FormData();
      for (let elemento in this.postulante) {
        fd.append(elemento, this.postulante[elemento]);
      }

      this.complemento = this.postulante.complemento;
      axios
        .post(
          Service.getUrlAPI() + "admision/postulante",
          fd,
          {
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization: Service.getTokenExt(),
            },
          }
        )
        .then((response) => {
          this.send_loading = false;
          if (response.status === 201) {
            //this.checkDialog = false;
            this.response_status = true;
            this.postulante.deposito = "";
            this.postulante.deposito_id = "";
            this.toast({ color: "success", text: response.data });
            this.type_state = 1;
            if (this.$refs.formd) this.$refs.formd.reset();
          } else if (response.status === 200) {
            this.toast({ color: "info", text: response.data });
          }
        })
        .catch((error) => {
          this.send_loading = false;
          this.toast({ color: "info", text: error.response.data });
          //console.error("Error, no se pudo guardar", error);
        });
    },

    printForm() {
      axios
        .post(
          Service.getUrlAPI() + "admision/comprobante",
          {
            convocatoria_id: this.postulante.convocatoria_id,
            cedula_dni: this.cedula,
            fecha_nacimiento: this.fecha_nacimiento,
            complemento: this.complemento,
            modalidad_id : this.postulante.modalidad_id
          },
          Service.getHeader()
        )
        .then(async (response) => {
          let datos = response.data;
          let codesize = datos.id.toString().length;
          let codigo = "0".repeat(10 - codesize);
          codigo = codigo + datos.id;
          var bodyPdf = await PDFService.bodyPDF(datos, codigo);
          var docDefinition = {
            pageSize: "LETTER",
            pageMargins: [40, 55, 40, 35],
            header: PDFService.setting().header,
            footer: PDFService.setting().footer,
            content: bodyPdf,
          };
          console.log("this.postulante.modalidad_id",this.postulante.modalidad_id);
          //pdfMake.createPdf(docDefinition).download("FormularioPostulacion.pdf");
          pdfMake.createPdf(docDefinition).open();
        })
        .catch((err) => {
          console.error(err);
        });
    },

    formatDate(fecha) {
      if (!fecha) return null;

      const [year, month, day] = fecha.substring(0, 10).split("-");
      return `${day}/${month}/${year}`;
    },

    isText: function (evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      const codes = [45,46,192,193,196,200,201,203,204,205,207,210,211,214,217,218,220,224,225,228,232,233,235,236,237,239,242,243,246,249,250,252];
      if (codes.includes(charCode) ||
        charCode == 39 ||
        charCode == 32 ||
        charCode == 209 ||
        charCode == 241 ||
        (65 <= charCode && charCode <= 90) ||
        (97 <= charCode && charCode <= 122)
      ) {
        return true;
      } else {
        evt.preventDefault();
        return false;
      }
    },
  },
};
